import React from 'react';
import classes from './Dialogs.module.css';
import DialogItem from './DialogItem/DialogItem';
import Message from './Message/Message';
import { Redirect } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../../utils/validators/validators';
import { Textarea } from '../../common/FormsControls/FormsControls';

const Dialogs = (props) => {

	let state = props.messagesPage;

	let dialogsElements = state.dialogs.map(d => <DialogItem name={d.name} id={d.id} key={d.id} />);
	let messagesElements = state.messages.map(m => <Message message={m.message} id={m.id} time={m.time} name={m.name} key={m.id} />);

	let addNewMessage = (values) => {
		props.sendMessage(values.newMessageBody);
	}

	if (!props.isAuth) return <Redirect to={'/login'} />;

	return (
		<div className={classes.dialog_wrapper}>
			<div className={classes.dialogs}>
				<div className={classes.dialogs_title}>Dialogs</div>
				{dialogsElements}
			</div>

			<div className={classes.messages}>
				{messagesElements}
				<AddMessageFormRedux onSubmit={addNewMessage} />
			</div>
		</div>
	);
}

const maxLength50 = maxLengthCreator(50);

const AddMessageForm = (props) => {
	return (
		<form onSubmit={props.handleSubmit}>
			<div className={classes.newMessage}>
				<Field component={Textarea} name="newMessageBody" validate={[required, maxLength50]} placeholder="Enter your message..." />
				<button>Send</button>
			</div>
		</form>
	)
}

const AddMessageFormRedux = reduxForm({ form: "dialogAddMessageForm" })(AddMessageForm);

export default Dialogs;